import Button from 'components/Button/Button'
import TextField from 'components/Form/TextField'
import Link from 'components/Link/Link'
import { html } from 'template'
import './AuthForm.scss'

const submitHandler = (e) => {
  e.preventDefault()
}

export default function LoginForm () {
  return html`
<form class="auth-form" autocomplete="off" ${{ onsubmit: submitHandler }}>
  <h2 class="auth-form__title"></h2>
  ${TextField({ label: '', name: 'login', invalid: true, feedback: ' ' })}
  ${TextField({ label: '', name: 'password', type: 'password' })}

  <div class="auth-form__actions">
    ${Button({ label: '', type: 'submit', className: 'auth-form__submit' })}
    ${Link({ label: ' ?', href: '/registration' })}
  </div>
</form>`
}
