import Button from 'components/Button/Button'
import TextField from 'components/Form/TextField'
import Link from 'components/Link/Link'
import Modal, { showModal } from 'components/Modal/Modal'
import { html } from 'template'
import AvatarModal from './AvatarModal'
import DataList from './DataList'
import './Profile.scss'
import ProfileAvatar from './ProfileAvatar'

const avatarModalID = 'modal-avatar'
const changeDataModalID = 'modal-change-data'
const changePasswordModalID = 'modal-change-password'

const data = [
  { label: '', name: 'email', value: 'pochta@yandex.ru' },
  { label: '', name: 'login', value: 'ivanivanov' },
  { label: '', name: 'first_name', value: '' },
  { label: '', name: 'second_name', value: '' },
  { label: '  ', name: 'display_name', value: '' },
  { label: '', name: 'phone', value: '+7 (909) 967 30 30' }
]

const actions = [
  { label: Link({ label: 'Change data', tag: 'button', onclick: showModal(changeDataModalID) }) },
  { label: Link({ label: 'Change password', tag: 'button', onclick: showModal(changePasswordModalID) }) },
  { label: Link({ label: 'Logout', variant: 'danger', tag: 'button' }) }
]

export default function Profile () {
  return html`
<div class="profile">
  ${ProfileAvatar({ onclick: showModal(avatarModalID), onopen: (e) => { console.log(e) } })}
  <span class="profile__username">Ivan</span>
  ${DataList({ items: data, className: 'profile__data' })}
  ${DataList({ items: actions, className: 'profile__actions' })}
  ${AvatarModal({ id: avatarModalID })}
  ${ChangeDataModal}
  ${ChangePasswordModal}
</div>`
}

function ChangeDataModal () {
  const children = html`
<div class="modal__title"> </div>
<form class="modal__body" ${{ onsubmit: (e) => { e.preventDefault() } }}>
  ${data.map(TextField)}
  ${Button({ label: '', className: 'w-100 mt-8' })}
</form>`

  return Modal(children, { id: changeDataModalID })
}

function ChangePasswordModal () {
  const children = html`
<div class="modal__title"> </div>
<form class="modal__body" ${{ onsubmit: (e) => { e.preventDefault() } }}>
  ${TextField({ label: ' ', name: 'oldPassword', type: 'password' })}
  ${TextField({ label: ' ', name: 'newPassword', type: 'password' })}
  ${TextField({ label: '  ', name: 'newPasswordConfirm', type: 'password' })}
  ${Button({ label: '', className: 'w-100 mt-8' })}
</form>`

  return Modal(children, { id: changePasswordModalID })
}
